<template>
  <div class="login_page">
    <div class="login_image">
      <div class="dark_shadow">

      </div>
      <div class="auth_login">
        <img src="../../assets/images/logo.svg" alt="">
      </div>
      <div class="auth_title">
        The ultimate multipurpose dashboard UI Kit for kickstart any project.
      </div>
    </div>
    <div class="login_form">
      <div class="main_form">
        <div class="login_title">Sign in</div>
        <div class="main_forms">
          <form @submit.prevent="sign_in()">
            <div class="login_text_input">
              <label>
                Email
              </label>
              <div class="login_input">
                <input v-model="email" type="text" placeholder="">
              </div>
            </div>
            <div class="login_text_input">
              <label >
                Password
              </label>
              <div class="login_input">
                <input v-model="password" type="password" placeholder="">
              </div>
            </div>

            <div class="form_button">

              <button :disabled="isCheck">
                <span v-if="!isCheck">Sign in </span>
                <b-spinner v-if="isCheck"></b-spinner>

              </button>
            </div>
          </form>

        </div>

      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      email:'',
      password:'',
      isCheck:false,
    }
  },
  methods:{
    sign_in(){
      this.isCheck=true
      this.$http.post('manager/signin/',{
        email:this.email,
        password:this.password,
      })
          .then(res=>{
            localStorage.setItem('token',res.data.access)
            this.$router.push('/')
            this.isCheck=false
          })
          .catch(error=>{
            console.log(error.response.data.email)
            this.$toast(`${error.response.data.email}`, {
              timeout: 2000,
              type:'error'
            });

            this.isCheck=false
          })
    }
  }
}
</script>
