<template>
  <div class="home">
    <Sidebar></Sidebar>
    <div>

      <b-modal id="modal-1" title="Status">
        <div class="col-sm-10">
          <select class="form-control" @input="send(datamain)" v-model="status" name="status">
            <option value="pending">Pending</option>
            <option value="approved">Approved</option>
            <option value="rejected">Rejected</option>
          </select>
        </div>
      </b-modal>
    </div>

    <section class="main_section">
      <div class="main_container">

        <div class="section-2">
          <div class="d-flex justify-content-between">
            <div class="dash_card_title">
              Send transaction
            </div>
            <div>
              <button class="btn btn-primary" @click="$router.push('/coins/create')"> Create</button>
            </div>
          </div>
          <div class="main_cards">
            <table >
              <thead>
              <tr class="border p-2" >
                <th class="fr_table">id</th>
                <th>coin</th>
                <th>amount</th>
                <th>status</th>

              </tr>
              </thead>
              <tbody >
              <tr v-for="item in data " class="border p-2"  :key="item">
                <td class="border p-2" >
                  <div>
                    {{item.id}}
                  </div>
                </td>
                <td class="border p-2" >
                  <div>
                    {{item.coin}}

                  </div>
                </td>
                <td class="border p-2" >
                  <div>
                    {{item.amount}}

                  </div>
                </td>
                <td class="border p-2" >
                  <div>
                    {{item.status}}

                  </div>
                </td>

                <td class="d-flex border p-2" >
                  <div>
                    <button  @click="datamain=item.id" v-b-modal.modal-1 class="mx-2 my-2 btn btn-100 btn-primary">
                      Edit
                    </button>
                  </div>
                  <div>
                    <button class="btn my-2 btn-100 btn-danger">
                      Delete
                    </button>
                  </div>
                </td>
              </tr>

              </tbody>
            </table>
          </div>
        </div>
      </div>

    </section>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  components:{
    Sidebar,
  },
  data(){
    return{
      data:[],
      status:'',
      datamain:''
    }
  },
  mounted() {
    this.$http.get('manager/receive-transaction/',{
      headers:{
        Authorization:`Bearer ${localStorage.getItem('token')}`
      }
    })
        .then(res=>{
          this.data=res.data
        })
    // C
  },

  methods:{
    send(item){
      this.isCheck=true
      this.$http.patch(`manager/receive-transaction/${item}/`,{
            "status":this.status

          },
          {
            headers:{
              Authorization:`Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(()=>{
            this.isCheck=false
            this.$toast(`Success`, {
              timeout: 2000,
              type:'success'
            });
            this.$http.get('manager/receive-transaction/',{
              headers:{
                Authorization:`Bearer ${localStorage.getItem('token')}`
              }
            })
                .then(res=>{
                  this.data=res.data
                })
          })
          .catch(error=>{
            this.isCheck=false
            this.$toast(`${error.response.status}`, {
              timeout: 2000,
              type:'error'
            });
          })
    },
    send1(){
      this.$http.post('/receive-transaction/',{
            "coin":this.coin,
            "amount":this.price,
            "recipientTgUsername":this.telegram_username

          },
          {
            headers:{
              Authorization:`Bearer ${localStorage.getItem('token')}`
            }
          })
          .then(res=>{
            this.data=res.data
          })
          .catch(error=>{
            this.isCheck=false
            this.$toast(`${error.response.data}`, {
              timeout: 2000,
              type:'error'
            });
          })
    }
  }
}
</script>
