<template>
  <div class="home">
    <Sidebar></Sidebar>

    <section class="main_section">
      <div class="main_container">

        <div class="section-2">
          <div class="d-flex justify-content-between">
            <div class="dash_card_title">
              Coins create
            </div>
            <div>
              <button class="btn btn-primary"> Create</button>
            </div>
          </div>
          <div class="main_cards">
            <div class="col-xl-12">
              <div>
                <b-tabs content-class="mt-3">
                  <b-tab title="Eng" active>
                    <form @submit.prevent="create()">

                      <div class="row">
                        <div class="col-xl-4">
                          <div class="login_text_input">
                            <label>
                              name
                            </label>
                            <div class="login_input">
                              <input v-model="name" type="text" placeholder="">
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="col-xl-12">
                        <button class="btn btn-primary w-100"  >Create </button>
                      </div>
                    </form>
                  </b-tab>
                  <b-tab title="Rus">
                    <form @submit.prevent="createru()">

                      <div class="row">
                        <div class="col-xl-4">
                          <div class="login_text_input">
                            <label>
                              name
                            </label>
                            <div class="login_input">
                              <input v-model="nameru" type="text" placeholder="">
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="col-xl-12">
                        <button class="btn btn-primary w-100"  >Create </button>
                      </div>
                    </form>
                  </b-tab>
                  <b-tab title="Uz" >
                    <form @submit.prevent="createuz()">

                      <div class="row">
                        <div class="col-xl-4">
                          <div class="login_text_input">
                            <label>
                              name
                            </label>
                            <div class="login_input">
                              <input v-model="nameuz" type="text" placeholder="">
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="col-xl-12">
                        <button class="btn btn-primary w-100"  >Create </button>
                      </div>
                    </form>
                  </b-tab>
                </b-tabs>
              </div>
            </div>
          </div>
        </div>
      </div>

    </section>
  </div>
</template>
<script>
import Sidebar from "@/components/Sidebar.vue";
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
  components:{
    Sidebar,
  },
  data(){
    return{
      name:'',
      symbol:'',
      about:'',
      website:'',
      whitepaper:'',
      nameru:'',
      symbolru:'',
      aboutru:'',
      websiteru:'',
      whitepaperru:'',
      nameuz:'',
      symboluz:'',
      aboutuz:'',
      websiteuz:'',
      whitepaperuz:''
    }
  },
  methods:{
    create(){
      this.$http.post('admin/company-info/',{
        'name':this.name,

      },{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
    },
    createru(){
      this.$http.post('admin/company-info/',{
        'name':this.nameru,

      },{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
    },
    createuz(){
      this.$http.post('admin/company-info/',{
        'name':this.nameuz,

      },{
        headers:{
          Authorization:`Bearer ${localStorage.getItem('token')}`,
          'Accept-Language': 'en'

        }
      })
    }
  }
}
</script>
